@import url(https://fonts.googleapis.com/css2?family=Klee+One:wght@600&display=swap);
*{padding:0;margin:0;box-sizing:border-box}body{background-color:#4158D0;background-image:linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);font-family:'Klee One', cursive}.container{display:flex;justify-content:center;align-items:center;height:100vh}.container::before{content:'';position:absolute;width:100%;height:100%;background-color:rgba(0,0,0,0.2)}.container .box{height:450px;width:400px;padding:20px 10px;background-color:rgba(255,255,255,0.308);border-radius:10px;box-shadow:0 0 2px 2px rgba(255,255,255,0.1);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px)}.container .box .title{font-size:2rem;display:block;text-align:center;color:#000;font-weight:500;letter-spacing:2px}.container .box .error{font-size:1.3rem;text-align:center;color:red;text-shadow:0 0 1px black;font-weight:bold}.container .city .city-img{display:flex;justify-content:center;align-items:center}.container .city .city-img img{width:200px}.container .city .search-box{padding:10px 20px;display:flex;justify-content:center;align-items:center;flex-direction:column}.container .city .search-box p{font-size:1.5rem}.container .city .search-box form{display:flex;justify-content:center;align-items:center;flex-direction:column;width:100%}.container .city .search-box input{margin-top:10px;padding:10px;text-align:center;height:40px;width:70%;border-radius:50px;outline:none;color:white;border:2px solid rgba(0,0,0,0.363);font-size:17px;background:rgba(0,0,0,0.2);transition:0.5s}.container .city .search-box input:hover,.container .city .search-box input:focus{width:80%}.container .city .search-box input::-webkit-input-placeholder{color:rgba(255,255,255,0.616)}.container .city .search-box input:-ms-input-placeholder{color:rgba(255,255,255,0.616)}.container .city .search-box input::placeholder{color:rgba(255,255,255,0.616)}.container .city .search-box button{margin-top:5px;width:100px;height:35px;border-radius:50px;outline:none;border:2px solid skyblue;font-size:16px;cursor:pointer;transition:0.5s;font-weight:weight(medium);background-color:transparent}.container .city .search-box button:hover{width:120px;background-color:skyblue}.container .temp-detail .temp{margin-top:20px;padding:0px 30px;display:flex;justify-content:center;align-items:center;justify-content:space-between}.container .temp-detail .temp .details{font-size:18px}.container .temp-detail .temp .img img{width:100px}.container .temp-detail .city-name{font-size:2.3rem;text-align:center;font-weight:600}.container .temp-detail .weather-info{padding:10px}.container .temp-detail .weather-info .p{margin-bottom:15px;text-align:center;font-size:1.4rem;font-weight:500}.container .temp-detail .weather-info .sub-menu{text-align:center;display:flex;justify-content:center;align-items:center;margin:5px 10px;flex-direction:row;justify-content:space-evenly;flex-wrap:wrap}.container .temp-detail .weather-info .sub-menu img{width:40px}

